/* eslint-disable @typescript-eslint/no-shadow */

/* eslint-disable react/jsx-props-no-spreading */
import * as React from 'react';

import { range } from 'd3-array';
import { randomNormal } from 'd3-random';
import {
  scaleDiverging,
  scaleDivergingSqrt,
  scaleOrdinal,
  scaleQuantile,
  scaleQuantize,
  scaleSequential,
  scaleSequentialLog,
  scaleSequentialQuantile,
  scaleSequentialSqrt,
  scaleSqrt,
  scaleThreshold,
} from 'd3-scale';
import {
  interpolateBlues,
  interpolatePiYG,
  interpolateRdBu,
  interpolateTurbo,
  interpolateViridis,
  schemePurples,
  schemeRdBu,
  schemeSpectral,
} from 'd3-scale-chromatic';

import { ParentSize } from '@visx/responsive';

import D3Legend from './D3Legend';
import VisxLegend, { Props as VisxLegendProps } from './VisxLegend';

export type Props = {
  width: number;
  height: number;
  data?: Item[];
};

export type Item = {
  title: string;
};

const Content = React.memo((props: Props) => {
  // eslint-disable-next-line
  const { width, height } = props;
  const items = React.useMemo<VisxLegendProps[]>(
    () => [
      {
        title: 'scaleSequential([0, 1000], interpolateViridis)',
        color: scaleSequential([0, 1000], interpolateViridis),
      },
      {
        title: 'scaleSequentialSqrt([0, 1], interpolateTurbo)',
        color: scaleSequentialSqrt([0, 1], interpolateTurbo),
      },
      {
        title: 'scaleDiverging([-0.1, 0, 0.1], interpolatePiYG)',
        color: scaleDiverging([-0.1, 0, 0.1], interpolatePiYG),
        tickFormat: '+%',
      },
      {
        title: 'scaleDivergingSqrt([-0.1, 0, 0.1], interpolateRdBu)',
        color: scaleDivergingSqrt([-0.1, 0, 0.1], interpolateRdBu),
        tickFormat: '+%',
      },
      {
        title: 'Energy(joules): scaleSequentialLog([1, 100], interpolateBlues)',
        color: scaleSequentialLog([1, 100], interpolateBlues),
        ticks: 10,
      },
      {
        title: 'Quantile: scaleSequentialQuantile(range(100).map(() => Math.random() ** 2), interpolateBlues)',
        color: scaleSequentialQuantile(
          range(100).map(() => Math.random() ** 2),
          interpolateBlues
        ),
        tickFormat: '.2f',
      },
      {
        title: 'Temperature: scaleSqrt([-100, 0, 100], ["blue", "white", "red"]',
        color: scaleSqrt([-100, 0, 100], ['blue', 'white', 'red']),
      },
      {
        title: 'Unemployment rate: scaleQuantize([1, 10], schemePurples[9])',
        color: scaleQuantize([1, 10], schemePurples[9]),
      },
      {
        title: 'Height: scaleQuantile(range(1000).map(randomNormal(100, 20)), schemeSpectral[9])',
        color: scaleQuantile(range(1000).map(randomNormal(100, 20)), schemeSpectral[9]),
        tickFormat: '.0f',
      },
      {
        title: 'Unemployment rate: scaleThreshold([2.5, 3.1, 3.5, 3.9, 6, 7, 8, 9.5], schemeRdBu[9]',
        color: scaleThreshold([2.5, 3.1, 3.5, 3.9, 6, 7, 8, 9.5], schemeRdBu[9]),
        tickSize: 0,
      },
      {
        title:
          'Age: scaleOrdinal(["<10", "10-19", "20-29", "30-39", "40-49", "50-59", "60-69", "70-79", "â‰¥80"], schemeSpectral[10])',
        color: scaleOrdinal(
          ['<10', '10-19', '20-29', '30-39', '40-49', '50-59', '60-69', '70-79', 'â‰¥80'],
          schemeSpectral[10]
        ),
      },
    ],
    []
  );

  return (
    <>
      <h3>Color Legend Examples</h3>
      <ul>
        {items.map((props, i) => {
          const { title } = props;
          return (
            <li key={i}>
              <h4>{title}</h4>
              <D3Legend {...props} />
              <VisxLegend {...props} />
            </li>
          );
        })}
      </ul>
    </>
  );
});

const ColorLegend = React.memo(() => <ParentSize>{({ width }) => <Content width={width} height={300} />}</ParentSize>);

export default ColorLegend;
