import * as React from 'react';

import { Legend, Options } from './Legend';

export type Props = {
  className?: string;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  color: any;
} & Options;

const D3Legend = React.memo((props: Props) => {
  const { className, color, ...options } = props;

  const refRoot = React.useRef<HTMLDivElement>(null);

  React.useEffect(() => {
    const svg = Legend(color, options);
    if (refRoot.current && svg) {
      refRoot.current.innerHTML = '';
      refRoot.current.appendChild(svg);
    }
  }, []);
  return <div className={className} ref={refRoot} />;
});

export default D3Legend;
